/**
App
 */
* {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body, .App, #root {
  height: 100vh; }

.App {
  align-items: stretch;
  display: flex;
  flex-direction: row; }

.mapHolder {
  flex: 1.5 1;
  border: skyblue 2px solid;
  border-top: skyblue 4px solid;
  display: flex; }

.mapHolderLoading {
  flex: 1.5 1;
  border: skyblue 2px solid;
  border-top: skyblue 0px solid;
  display: flex;
  flex-direction: column; }

/**
Map
 */
#map {
  flex: 1 1; }

.marker {
  text-align: center; }

.subTextMarker {
  font-size: 80%; }

.popUpMouseOver {
  text-align: center;
  font-size: 90%; }

.popUpMouseOver > .subTextMarker {
  font-size: 80%; }

/*
Linker deel
 */
.brtInfo {
  flex: 1 1;
  display: flex;
  flex-direction: column; }

.header {
  margin: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row; }

.header > img {
  max-height: 3rem;
  margin-left: 1%; }

.header > h1 {
  color: black;
  margin: auto 1% auto 1%; }

.header > * {
  display: inline; }

.searchBar {
  padding: 1%; }

.footer {
  text-align: center;
  margin: 1%; }

/**
Linker deel resultaten stuk
 */
.resultsContainer {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
  border: solid skyblue 2px;
  border-radius: 10px;
  margin: 2% 7%; }

.resultPartContainer {
  flex: 1 1;
  overflow: auto; }

.backButton {
  width: 100%;
  padding: 0.5% 2%;
  color: darkblue;
  font-style: italic; }

.loadingButton {
  border-bottom: 0; }

.nonLoadingButton {
  border-bottom: 4px lightblue solid; }

.cursorPointer {
  cursor: pointer; }

.startScreen {
  padding: 2% 5% 1%; }

.boldHeaderText {
  font-weight: bold;
  text-align: center; }

ul {
  padding: 0; }

.liResultScreen {
  list-style: none;
  text-align: center;
  width: 94%;
  margin: 2% 3% 2.5%;
  padding: 2%;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  cursor: pointer; }

.hoofdText {
  font-size: 110%;
  margin-bottom: 0%; }

.subText {
  font-size: 80%; }

.objectScreen {
  padding: 5%; }

.objectScreen > h1 {
  margin-bottom: 1%; }

.objectScreen > h3 {
  color: #2C84CB;
  margin: 1% 0% 3%; }

.objectScreen hr {
  margin: 0% 0% 3% 0%; }

.namenTable {
  margin-bottom: 2%;
  width: 100%; }

.namenTable * {
  margin-bottom: 0; }

.verkoperKoper {
  vertical-align: top; }

.namenTable li {
  list-style: none; }
  .namenTable li:before {
    content: '-';
    margin-left: -20px;
    margin-right: 3px; }

.namenTable td:last-child {
  width: 75%; }

.attributeSectionObjectScreen {
  table-layout: fixed;
  width: 100%; }

.attributeSectionObjectScreen td:last-child {
  overflow-wrap: break-word;
  width: 55%; }

table {
  word-break: break-all; }

.dropDownItem * {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1em; }

.cogIcon {
  padding-left: 1%;
  float: left; }

.contextMenuContainer {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  z-index: 500;
  background: white;
  text-align: left;
  border-radius: 10px; }

.contextMenuContainer > p > b {
  font-size: 80%; }

.contextMenuContainer > p > span {
  font-size: 70%; }

.contextMenuContainer > p {
  white-space: nowrap;
  display: block;
  margin: 0;
  padding: 10px 10px;
  border-radius: 10px; }
  .contextMenuContainer > p:hover {
    cursor: pointer;
    padding: 9px 9px;
    box-sizing: border-box;
    border: #2c84cb 1px solid; }

